import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CheckFormService {

  constructor() { }

  checkName(name) {
    return name != undefined;
  }

  checkLogin(login) {
    return login != undefined;
  }

  checkPassword(password) {
    return password != undefined;
  }

  checkEmail(email) {
    return email != undefined;
  }
}
