import { Component, OnInit } from '@angular/core';
import { AuthService } from "../auth.service";
import { ArticleComponent } from "../article/article.component";
import { FlashMessagesService } from "angular2-flash-messages";
import { LanguageService } from "../language.service";
// @ts-ignore
import * as data from '../../assets/languages.json';
import {ArticlesService} from "../articles.service";
import {Router} from "@angular/router";

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  Languages: any;
  search: String;

  constructor(
    private flashMessages: FlashMessagesService,
    private articlesService: ArticlesService,
    public authService: AuthService,
    private languageService: LanguageService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.Languages = this.languageService.ReturnLanguages()["navbar"];
  }

  SearchArticle(): boolean {
    console.log(this.search)
    if (this.search == undefined) {
      this.flashMessages.show("Нет текста для поиска", {
        cssClass: 'alert-danger',
        timeout: 4000
      });
      return false;
    }
    return true;
    //this.articlesService.SearchArticlesByTitle(this.search)
  }
}
