import { Component, OnInit } from '@angular/core';
import { CheckFormService} from "../check-form.service";
import { AuthService } from "../auth.service";
import { FlashMessagesService } from "angular2-flash-messages";
import { Router } from '@angular/router';


@Component({
  selector: 'app-reg',
  templateUrl: './reg.component.html',
  styleUrls: ['./reg.component.scss']
})
export class RegComponent implements OnInit {

  name: String;
  email: String;
  password: String;
  res: any;

  constructor(
    private checkForm: CheckFormService,
    private flashMessages: FlashMessagesService,
    private router: Router,
    private authService: AuthService
  ) { }

  ngOnInit(): void {
  }

  UserRegisterClick() {
    const user = {
      name: this.name,
      email: this.email,
      password: this.password
    }
    let checkInfo:boolean = true

    if(!this.checkForm.checkEmail(user.email)){
      this.flashMessages.show("Name is undefined", {
        cssClass: 'alert-danger',
        timeout: 4000
      });
      checkInfo = false;
    }

    if(!this.checkForm.checkEmail(user.email)){
      this.flashMessages.show("Email is undefined", {
        cssClass: 'alert-danger',
        timeout: 4000
      });
      checkInfo = false;
    }

    if(!this.checkForm.checkPassword(user.password)) {
      this.flashMessages.show("Password is undefined", {
        cssClass: 'alert-danger',
        timeout: 4000});
      checkInfo = false;
    }

    if (!checkInfo)
    {
      return false;
    }

    this.authService.registerUser(user).subscribe(({message, success}) => {
      if (!success) {
        this.flashMessages.show(message, {
          cssClass: 'alert-danger',
          timeout: 4000
        })
        this.router.navigate(['/reg']);
      } else {
        this.flashMessages.show(message, {
          cssClass: 'alert-success',
          timeout: 4000
        })
        this.router.navigate(['/auth']);
      }
    })

  }
}
